  }

  .item:nth-of-type(4n+1) {
    -webkit-transform: scale(0.78, 0.78) rotate(5deg);
            transform: scale(0.78, 0.78) rotate(5deg);
    transition: all 0.35s;
  }
  
  .item:nth-of-type(4n+2) {
    -webkit-transform: scale(0.70, 0.70) rotate(-3deg);
            transform: scale(0.70, 0.70) rotate(-3deg);
    transition: all 0.35s;
  }
  
  .item:nth-of-type(4n+3) {
    -webkit-transform: scale(0.78, 0.78) rotate(5deg);
            transform: scale(0.78, 0.78) rotate(5deg);
    transition: all 0.35s;
  }
 
  .item:hover {
    -webkit-filter: none;
            filter: none;
    -webkit-transform: scale(1, 1) rotate(0deg) !important;
            transform: scale(1, 1) rotate(0deg) !important;
