    position: relative;
  }

.project-titles {
    width: 100%;
    font-family: sans-serif;
    font-size: 40px;
    position: relative;
  }

.asla-tracker {
    transition: all .5s ease-in-out;
}

.asla-tracker:hover {
    opacity: 1;
    -webkit-transform: scale(1.005);
            transform: scale(1.005);
}

.project-content a h3 {
    display: inline-block;
}

.asla {
    list-style: none;
    padding: 0px;
    display: block;
    -webkit-margin-before: 1.5em;
            margin-block-start: 1.5em;
    -webkit-margin-after: 1.5em;
            margin-block-end: 1.5em;
    -webkit-margin-start: 0;
            margin-inline-start: 0;
    -webkit-margin-end: 0;
            margin-inline-end: 0;
    margin-top: 0;

}

.major-content:nth-of-type(2n+1) .proj-image {
    grid-column-start: 1;
    grid-column-end: 8;
}
.major-content:nth-of-type(2n+1) .project-content { 
    grid-column-start: 7;
    grid-column-end: -1;
    text-align: right;
}
.major-content {
    position: relative;
    display: grid;
    padding: 5vh;
    grid-gap: 10px;
    gap: 10px;
    grid-template-columns: repeat(12, 1fr);
    align-items: center;
    text-align: -webkit-match-parent;
}

.project-content {
    grid-row-start: 1;
    grid-column-start: 1;
    grid-row-end: -1;
    grid-column-end: 7;
}

@-webkit-keyframes slide {
    100% { left: 0; }
}

@keyframes slide {
    100% { left: 0; }
}

.project-content a {
    text-decoration: none;
    color:  var(--text-primary);
}
.project-content a:hover {
    text-decoration: none;
    color:  var(--text-primary);
    opacity: .6;
}

.major-content:nth-of-type(2n+1) .tools {
    justify-content: flex-end;
}
.major-content .tools {
    display: flex;
    flex-wrap: wrap;
    position: relative;
    z-index: 2;
    padding: 0px;
    list-style: none;
}

ul ul {
    -webkit-margin-before: 0px;
            margin-block-start: 0px;
    -webkit-margin-after: 0px;
            margin-block-end: 0px;
}

.tools {
    list-style: none;
}

.major-content:nth-of-type(2n+1) .tools li {
    margin-top: 0px;
    margin-right: 0px;
    margin-bottom: 5px;
    margin-left: 20px;
    white-space: nowrap;
}

.major-content .tools li {
    margin-top: 0px;
    margin-right: 20px;
    margin-bottom: 5px;
    margin-left: 0px;
    white-space: nowrap;
}

.tools {
